import crudService from "../../crudService";

var route = 'base/line';
class lineService {

  //   
  getOne(data) {
    return crudService.getOne(route + '/', data)
  }
  getLinesByFact(data) {
    return crudService.getOne(route + '/fact/', data)
  }
}

export default new lineService();



