const user = JSON.parse(localStorage.getItem('user'))
const initialState = user
  ? { status: { loggedIn: true }, user }
  : { status: {}, user: null }

const state = {
  access_token: null,
  account: [],        //   


  accountList: [],
  cartList: [],
  item: [],           //   
  itemList: [],       //  
  orderList: [],      //  
  buyList: [],        //   
  qnaList: [],        //   
  reviewList: [],     //  

  chat: [],
  chatList: [],
  chatRoomNumber: [],

  search: {
    mode: false,
    keyword: '',
    option: '',
    sort: '',
    page: ''
  },

  operationStatus: ''

}
export default state;


